.toolbar {
	display: flex;
	flex-direction: column;

	font-family: var(--font-main);
}

.mainToolbar {
	display: flex;
	flex-wrap: wrap;

	background: var(--color-editor-bg-toolbar);
	border-bottom: 1px solid var(--color-editor-border);

	gap: 2rem;
	padding: 1rem;

	z-index: 2;
}

.subToolbar {
	display: flex;

	height: 3rem;

	background: var(--color-background-grey);
	border-bottom: 1px solid var(--color-editor-border);

	padding: 0.5rem;

	z-index: 1;
}
