.spinnerContainer {
	display: flex;
	justify-content: center;
	align-items: center;

	padding: 1rem;

	width: 100%;
}

.spinner {
	font-size: 5rem;
	opacity: 0;
	animation: fadeIn 300ms ease-in, spin 1s linear infinite;
}

@keyframes fadeIn {
	to {
		opacity: 1;
	}
}

@keyframes spin {
	to {
		transform: rotate(360deg);
	}
}
