.editor {
	width: 70vw;

	margin: 3rem auto;

	background-color: var(--color-editor-bg);
	border: 1px solid var(--color-editor-border);
}

.content {
	max-height: 40rem;

	overflow-y: auto;
}

.editable {
	min-height: 29.7cm;

	width: 21cm;
	margin: 1rem auto;
	padding: 1rem;

	background-color: var(--color-editor-bg-editable);

	border: 1px solid var(--color-editor-border-editable);

	overflow: hidden;
}
